<template>
	<div class="detail">
		<header>
			<div class="header-return"  v-show='isShow'>
				<i class="iconfont icon-fanhui" @click="goBack"></i>
				<i class="iconfont icon-index"></i>
			</div>
			
			<div class="header-bar" v-show='!isShow'>
				<i class="iconfont icon-fanhui" @click="goBack"></i>
				<div>
					<span>商品详情</span>
					<span>商品评价</span>
				</div>
				<i class="iconfont icon-index"></i>
			</div>
		</header>
		<section ref='wrapper'>
			<div>
				<div class="swiper-main">
					<img style="width: 100%;height: 300px;" :src="goods.imgUrl" alt=""/>
				</div>
				
				<div class="goods-name">
					<h1>{{goods.name}}</h1>
					<div>{{goods.content}}</div>
				</div>
				<div class="goods-price">
					<div class="oprice">
						<span>￥</span>
						<b>{{goods.price}}</b>
					</div>
					<div class="pprice">
						<span>价格:</span>
						<del>￥{{goods.reprice}}</del>
					</div>
				</div>
				
				<div class="details">
					<div class="details-shpo">
						<span>商品详情</span>
					</div>
					<div class="details-content">
						<h3>作者简介</h3>
						<p>{{goods.author}}</p>
						<p>{{goods.author_content}}</p>
					</div>
					<div class="details-content">
						<h3>内容简介</h3>
						<p>{{goods.synopsis}}</p>
					</div>
					<div>
						<img style="width: 100%;height: 300px;" :src="goods.imgUrl" alt=""/>
						<img style="width: 100%;height: 300px;" :src="goods.imgUrl" alt=""/>
					</div>
				</div>
			</div>
		</section>
		<footer>
			<div class="add-cart" @click='addCart'>加入购物车</div>
			<div>立即购买</div>
		</footer>
	</div>
</template>

<script>
	import BetterScroll from 'better-scroll'
	import 'swiper/dist/css/swiper.css'
	import { swiper, swiperSlide } from 'vue-awesome-swiper'
	import http from '@/common/api/request.js'
	import { Toast } from 'mint-ui';
	export default{
		name:'Detail',
		data(){
		   return{
			    BetterScroll:'',
				isShow:true,
				goods:{},
			}
		 },
		 components: {
		   swiper,
		   swiperSlide
		 },
		 created(){
		 		
		 		this.id = this.$route.query.id;
		 		
		 		this.getData();
		 	},
		 mounted() {
			 
			this.BetterScroll = new BetterScroll(this.$refs.wrapper, {
						probeType: 3,
						bounce: false,
						click:true
					})
			
			this.BetterScroll.on('scroll',(pos)=>{
				let posY = Math.abs( pos.y );
				if( posY > -0 ){
					this.isShow = false;
				}else{
					this.isShow = true;
				}
			})
		 },
		 methods:{
			async getData(){
						let id = this.$route.query.id;
						let res = await http.$axios({
							url:'/api/goods/id',
							params:{
								id
							}
						})
						this.goods = res;
			 },
			 addCart(){
			             let id = this.$route.query.id;
			             http.$axios({
			                 url:'/api/addCart',
			                 method:"post",
			                 data:{
			                     goodsId:id
			                 },
			                 headers:{
			                     token:true
			                 }
			             }).then(res=>{
							 console.log(res)
			                 if( res.success ){
			                     Toast('添加购物车成功');
			                 }
			             })
			         },
			 // 返回上一页
			 goBack(){
				 this.$router.back()
			 }
		 }
	}
</script>

<style scoped>
	.details-content{
		padding: 5px 0;
	}
	.details-content h3{
		font-size: 18px;
		padding: 5px 0;
	}
	.details-content p{
		padding: 5px 0;
		font-size: 16px;
		color: #222;
	}
	.detail{
		display: flex;
		flex-direction: column;
		width: 100vw;
		height: 100vh;
		overflow: hidden;
	}
	header{
		position: fixed;
		left: 0;
		top:0;
		width: 100%;
		height: 1.173333rem;
		z-index: 99;
	}
	.header-return{
		width: 100%;
		height: 1.173333rem;
		display: flex;
		justify-content: space-between;
		align-items: center;
	}
	.header-return i{
		text-align: center;
		line-height: 34px;
		margin:0 10px;
		width: 34px;
		height: 34px;
		color: #fff;
		background-color: rgba(0, 0, 0, 0.3);
		border-radius: 50%;
		font-size: 26px;
	}
	.header-bar{
		display: flex;
		justify-content: space-between;
		align-items: center;
		width: 100%;
		height: 1.173333rem;
		background-color: #fff;
		font-size:0.426666rem;
	}
	.header-bar span{
		padding:0 0.266666rem;
	}
	.header-bar i{
		padding:0 0.266666rem;
		font-size:0.586666rem;
	}
	section{
		flex:1;
		overflow: auto;
	}
	.swiper-main{
		position: relative;
		width: 100%;
		height:10rem;
	}
	.swiper-container{
		width: 100%;
		height: 10rem;
	}
	.swiper-container img{
		width: 100%;
		height: 10rem;
	}
	.swiper-pagination{
		right: 20px;
		width: 100%;
		bottom:0.266666rem;
		text-align: right;
		color: #FFFFFF;
		font-size:0.426666rem;
	}
	.swiper-pagination-fraction, .swiper-pagination-custom, .swiper-container-horizontal > .swiper-pagination-bullets{
		left:-0.533333rem;
	}
	.goods-name{
		padding:0.533333rem 0.266666rem;
		border-bottom: 1px solid #CCCCCC;
	}
	.goods-name h1{
		font-size:0.533333rem;
		font-weight: 500;
	}
	.goods-name div{
		padding:0.08rem 0;
		font-size:0.373333rem;
		color: #999;
	}
	.goods-price{
		padding:0.533333rem 0.266666rem;
	}
	.goods-price span{
		font-size:0.32rem;
	}
	.oprice{
		color: red;	
	}
	.pprice{
		color: #999999;
		font-size:0.373333rem;
	}
	footer{
		display: flex;
		justify-content: center;
		align-items: center;
		position: fixed;
		bottom: 0;
		left: 0;
		height: 1.306666rem;
		width: 100%;
		border-top: 1px solid #cccccc;
		background-color: #fff;
	}
	footer div{
		line-height: 1.306666rem;
		font-size: 0.426666rem;
		width: 50%;
		text-align: center;
		color: #fff;
		background-color: red;
	}
	.add-cart{
		background-color: #FF9500;
	}
	.details-shpo{
		padding:10px 5px ;
		font-size: 20px;
		border-left: 2px solid darkgreen;
	}
</style>